@charset "UTF-8";
/* chota.css */
@import "../vendor/mono-icons/icons.css";
:root {
  --bg-color: #fff;
  --bg-secondary-color: #f3f3f6;
  --color-primary: #14854f;
  --color-lightGrey: #d2d6dd;
  --color-grey: #747681;
  --color-darkGrey: #3f4144;
  --color-error: #d43939;
  --color-success: #28bd14;
  --grid-maxWidth: 120rem;
  --grid-gutter: 2rem;
  --font-size: 1.6rem;
  --font-color: #333;
  --font-family-sans: -apple-system, "BlinkMacSystemFont", "Avenir",
    "Avenir Next", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  --font-family-mono: monaco, "Consolas", "Lucida Console", monospace;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  line-height: 1.15;
  text-size-adjust: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: var(--bg-color);
  line-height: 1.6;
  font-size: var(--font-size);
  color: var(--font-color);
  font-family: "Segoe UI", "Helvetica Neue", sans-serif; /* fallback */
  font-family: var(--font-family-sans);
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  margin: 0.35em 0 0.7em;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.75em;
}

h3 {
  font-size: 1.5em;
}

h4 {
  font-size: 1.25em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 0.85em;
}

a {
  color: var(--color-primary);
  text-decoration: none;
}

a:hover:not(.button) {
  opacity: 0.75;
}

button {
  font-family: inherit;
}

p {
  margin-top: 0;
}

blockquote {
  background-color: var(--bg-secondary-color);
  padding: 1.5rem 2rem;
  border-left: 3px solid var(--color-lightGrey);
}

dl dt {
  font-weight: bold;
}

hr {
  border: none;
  background-color: var(--color-lightGrey);
  height: 1px;
  margin: 1rem 0;
}

table {
  width: 100%;
  border: none;
  border-collapse: collapse;
  border-spacing: 0;
  text-align: left;
}

table.striped tr:nth-of-type(2n) {
  background-color: var(--bg-secondary-color);
}

td,
th {
  vertical-align: middle;
  padding: 1.2rem 0.4rem;
}

thead {
  border-bottom: 2px solid var(--color-lightGrey);
}

tfoot {
  border-top: 2px solid var(--color-lightGrey);
}

code,
kbd,
pre,
samp,
tt {
  font-family: var(--font-family-mono);
}

code,
kbd {
  font-size: 90%;
  white-space: pre-wrap;
  border-radius: 4px;
  padding: 0.2em 0.4em;
  background-color: var(--bg-secondary-color);
  color: var(--color-error);
}

pre {
  background-color: var(--bg-secondary-color);
  font-size: 1em;
  padding: 1rem;
  overflow-x: auto;
}

pre code {
  background: none;
  padding: 0;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

img {
  max-width: 100%;
}

fieldset {
  border: 1px solid var(--color-lightGrey);
}

iframe {
  border: 0;
}

.container {
  max-width: var(--grid-maxWidth);
  margin: 0 auto;
  width: 96%;
  padding: 0 calc(var(--grid-gutter) / 2);
}

.row {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin-left: calc(var(--grid-gutter) / -2);
  margin-right: calc(var(--grid-gutter) / -2);
}

.row.reverse {
  flex-direction: row-reverse;
}

.col {
  flex: 1;
}

.col,
[class*=" col-"],
[class^=col-] {
  margin: 0 calc(var(--grid-gutter) / 2) calc(var(--grid-gutter) / 2);
}

.col-1 {
  flex: 0 0 calc(8.3333333333% - var(--grid-gutter));
  max-width: calc(8.3333333333% - var(--grid-gutter));
}

.col-2 {
  flex: 0 0 calc(16.6666666667% - var(--grid-gutter));
  max-width: calc(16.6666666667% - var(--grid-gutter));
}

.col-3 {
  flex: 0 0 calc(25% - var(--grid-gutter));
  max-width: calc(25% - var(--grid-gutter));
}

.col-4 {
  flex: 0 0 calc(33.3333333333% - var(--grid-gutter));
  max-width: calc(33.3333333333% - var(--grid-gutter));
}

.col-5 {
  flex: 0 0 calc(41.6666666667% - var(--grid-gutter));
  max-width: calc(41.6666666667% - var(--grid-gutter));
}

.col-6 {
  flex: 0 0 calc(50% - var(--grid-gutter));
  max-width: calc(50% - var(--grid-gutter));
}

.col-7 {
  flex: 0 0 calc(58.3333333333% - var(--grid-gutter));
  max-width: calc(58.3333333333% - var(--grid-gutter));
}

.col-8 {
  flex: 0 0 calc(66.6666666667% - var(--grid-gutter));
  max-width: calc(66.6666666667% - var(--grid-gutter));
}

.col-9 {
  flex: 0 0 calc(75% - var(--grid-gutter));
  max-width: calc(75% - var(--grid-gutter));
}

.col-10 {
  flex: 0 0 calc(83.3333333333% - var(--grid-gutter));
  max-width: calc(83.3333333333% - var(--grid-gutter));
}

.col-11 {
  flex: 0 0 calc(91.6666666667% - var(--grid-gutter));
  max-width: calc(91.6666666667% - var(--grid-gutter));
}

.col-12 {
  flex: 0 0 calc(100% - var(--grid-gutter));
  max-width: calc(100% - var(--grid-gutter));
}

@media screen and (max-width: 599px) {
  .container {
    width: 100%;
  }
  .col,
  [class*=col-],
  [class^=col-] {
    flex: 0 1 100%;
    max-width: 100%;
  }
}
@media screen and (min-width: 900px) {
  .col-1-md {
    flex: 0 0 calc(8.3333333333% - var(--grid-gutter));
    max-width: calc(8.3333333333% - var(--grid-gutter));
